.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.logo {
  color: #d4dcff;
  height: 100%;
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 1.2rem;
  padding: 1rem;
}

.logo-img {
  width: 7rem;
  opacity: 0.8;
}

@media (max-width: 768px) {
  .logo {
    padding: 0;
  }

  .logo-img {
    width: 5rem;
  }
}

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  background: transparent;
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (max-width: 768px) {
  .main-header {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}

.main-navigation__menu-btn {
  height: 2.5rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 0rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2px;
  background: #d4dcff;
}

.main-navigation__title {
  color: #d4dcff;
}

.main-navigation__title a {
  text-decoration: none;
  color: #d4dcff;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  color: #d4dcff;
  display: block;
    padding: 9px 0;
    border: 1px solid transparent;
    border-radius: 18px;
    font-size: .8125rem;
    font-weight: 600;
    line-height: 1;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    box-sizing: border-box;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  color: #d4dcff;
}

.nav-links button {
  cursor: pointer;
  color: #d4dcff;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
  border: 1px solid #d4dcff;
  border-radius: 8px;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  color: #d4dcff;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: #d4dcff;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid #d4dcff;
    border-radius: 8px;
    color: #d4dcff;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    color: #d4dcff;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 100%;
  background: #070316;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}


@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

